@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

body {
    font-family: 'Poppins', sans-serif;
    max-width: 100%;
}

.head {
    text-align: center;
    font-size: 4rem;
}

.nav-item {
    padding: 10px 25px;
    text-align: center;
    font-size: 1.2rem;
    border-radius: 15px;
}

.nav-item:hover {
    transition: 0.3s;
}

nav a {
    text-decoration: none;
}

nav {
    border-bottom: 0.4px solid;
    margin-bottom: 25px;
}

.navbar-toggler i {
    font-size: 20px;
}

.mode {
    text-align: center;
    padding-top: 8px;
    padding-left: 10px;
    cursor: pointer;
}

.mode i {
    font-size: 30px;
}

.navbar-brand {
    font-size: 35px;
    text-align: center;
}


